.filters {
    width: 100%;
    background-color: var(--white);
    display: flex;
    gap: 10px;
}

.button {
    background: var(--white);
    width: 100px;
    height: 70px;
    border: 2px solid var(--white);
    box-shadow: 0px 15px 15px 2px rgba(43, 28, 1, 0.04);
    transition: 0.4s all;
}

.active {
    border-color: var(--yellow-light);
}

@media (max-width: 768px) {
    .filters {
        flex-wrap: wrap;
    }
}